@import '~assets/scss/pc/ui/slider.scss';
@import '~assets/scss/pc/ui/post.scss';

#top-container{
  width: 100vw;
  height: 100vh;

  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;

  color: white;

  & > h1{
    margin-top: get-vw(250px);
    z-index: 1;
    @include muller-80-bold;
  }

  & > p{
    width: get-vw(500px);
    text-align: center;
    z-index: 1;
    @include muller-16-medium;
  }
}

//categories

.categories{
  margin-top: get-vw(20px);
  z-index: 1;

  ul{
    display: grid;
    grid-auto-flow: column;
    grid-column-gap: get-vw(20px);
    @include muller-20-medium;

    li:first-child{
      a{
        display: flex;
        align-self: flex-start;
      }
      a::before{
        width: get-vw(18px);
        height: get-vw(20px);
        margin-right: get-vw(10px);
        align-self: center;

        display: inline-block;

        background-image: url('~assets/images/catalog.svg');
        background-size: 100% auto;
        background-position: center;
        background-repeat: no-repeat;
        content: '';
      }
    }

    li:not(:first-child){
      border-bottom: get-vw(2px) solid white;
      opacity: 0.5;
    }

    li:hover{
      opacity: 1;
    }
  }
}

//search

@mixin search-result{
  .search-result{
    a{
      display: grid;
      grid-auto-flow: column;
      grid-template-columns: get-vw(64px) 1fr;
      grid-auto-rows: get-vw(80px);
      grid-column-gap: get-vw(10px);
    }

    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: get-vw(10px);
    }

    &_info{
      display: grid;
      grid-row-gap: get-vw(0px);

      h2{ @include muller-30-bold; }
      span{
        & > :first-child{ opacity: 0.5; }
        @include muller-14-medium;
      }
    }
  }
}

.home-search{
  margin-top: get-vw(40px);
  position: relative;
  z-index: 2;

  &_search-bar{
    width: get-vw(600px);
    height: get-vw(50px);
    position: relative;
    z-index: 3;

    form{
      width: 100%;
      height: 100%;
      display: flex;
      background-color: $black-05;
      backdrop-filter: blur(16px);
      border: get-vw(2px) solid white;
      border-radius: get-vw(32px);
    }

    input{
      flex-grow: 1;
      padding-left: get-vw(25px);
      color: white;
      @include muller-20-medium;
    }

    button{
      width: get-vw(46px);
      height: get-vw(46px);
      border-radius: 50%;

      background-image: url('~assets/images/search.svg');
      background-size: get-vw(20px);
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &_live-results-wrapper{
    width: get-vw(600px);
    height: max-content;
    max-height: get-vw(50px);
    left: 0; top: 0;
    position: absolute;

    background-color: $black-05;
    backdrop-filter: blur(16px);
    border: get-vw(2px) solid white;
    border-radius: get-vw(26px);
    overflow: hidden;

    &.active{
      max-height: get-vw(492px);
    }
  }
  &_live-results{
    width: 100%;
    height: 100%;
    padding: 0 get-vw(25px);
    padding-top: get-vw(70px);
    padding-bottom: get-vw(25px);

    display: grid;
    grid-auto-rows: max-content;
    grid-row-gap: get-vw(22px);

    @include search-result;
  }
}

//anouncements

#posts-container{
  width: 100%;
  padding: get-vw(15px);
  margin-top: 100vh;

  position: absolute;
  display: grid;
  grid-auto-flow: row;
  grid-auto-rows: get-vw(157.5px);
  grid-template-columns: 1fr 1fr 1fr;

  background-color: white;
  border-radius: get-vw(30px);
}
