@mixin button-light{
    width: max-content;
    height: get-vw(42px);
    padding: 0 get-vw(16px);

    display: flex;
    align-items: center;

    border: get-vw(2px) solid white;
    border-radius: get-vw(22px);
    backdrop-filter: blur(8px);

    color: white;
}

@mixin button-dark{
    width: max-content;
    height: get-vw(42px);
    padding: 0 get-vw(16px);

    display: flex;
    align-items: center;

    border: get-vw(2px) solid $black;
    border-radius: get-vw(22px);
    backdrop-filter: blur(8px);

    color: $black;
}
