@import '~assets/scss/pc/ui/button.scss';

@mixin slide{
  .slide{
    width: 100vw;
    height: 100%;
    position: relative;
    color: white;
    z-index: 0;

    img{
      width: 100%;
      height: 100%;
      position: absolute;
      object-fit: cover;
      object-position: center;
      z-index: -1;
    }

    &_text-container{
      width: get-vw(900px);
      height: max-content;
      left: get-vw(40px);
      bottom: get-vw(80px);

      position: absolute;

      &_title{ @include muller-80-bold; }

      &_content{
        width: get-vw(740px);
        margin-top: get-vw(20px);
        @include muller-16-medium;
       }

      &_link{
        margin-top: get-vw(30px);
        @include button-light;
        @include muller-20-medium;
      }
    }
  }
}

.slider{
  width: 100vw;
  height: 100vh;
  left: 0; top: 0;

  position: absolute;
  overflow: hidden;

  &_slides{
    width: max-content;
    height: 100%;
    display: flex;
    transform: translateX(calc(-100vw * var(--slide, 1)));

    &.changing{
      transition: 0.3s ease-out;
    }

    @include slide;
  }

  &_controls{
    left: get-vw(90px);
    bottom: get-vw(30px);

    position: absolute;
    display: grid;
    grid-auto-flow: column;
    grid-column-gap: get-vw(10px);

    align-items: center;

    &_indicator{
      width: get-vw(12px);
      height: get-vw(5px);
      left: get-vw(23px);
      position: absolute;

      border-radius: get-vw(10px);
      background-color: white;
      transition: 0.25s;
    }

    &_go-to-slide{
      width: get-vw(12px);
      height: get-vw(5px);
      background-color: grey;
      border-radius: get-vw(10px);
    }

    &_prev-slide,
    &_next-slide{
      width: get-vw(12px);
      height: get-vw(12px);
      border-left: get-vw(2px) solid white;
      border-top: get-vw(2px) solid white;
      border-radius: get-vw(2px);
      transform: rotate(-45deg);
    }
    &_next-slide{
      transform: rotate(135deg);
    }
  }
}
