@import '../catalogue/categories';
@import '../catalogue/product-item';
@import '../catalogue/category-item';
@import '../catalogue/filters';

#catalog,
#catalogue,
#category,
#search{
  width: 100vw;
  height: max-content;
  display: flex;
  flex-direction: column;

  .slider{
    height: 90vh;

    .slide_text-container{
      bottom: get-vw(175px);
    }

    &_controls{
      bottom: get-vw(125px);
    }
  }
}

#title{
  width: max-content;
  margin: auto;
  left: 0; right: 0;
  top: get-vw(140px);
  position: absolute;
  color: white;
  z-index: 1;
  @include muller-80-bold;
}

.catalog-container{
  width: 100vw;
  height: max-content;
  padding: get-vw(30px);
  margin-top: 80vh;

  position: relative;
  display: grid;
  grid-template-areas:
  'controls controls'
  '. filters';
  grid-template-columns: max-content 1fr;
  grid-row-gap: get-vw(30px);
  grid-column-gap: get-vw(30px);

  background-color: white;
  border-radius: get-vw(30px);
  z-index: 1;
}

.catalog-controls{
  display: flex;
  justify-content: space-between;
  grid-area: controls;

  &_change-order{
    display: flex;
    align-items: center;

    div{
      width: max-content;
      height: get-vw(42px);
      margin-left: get-vw(15px);

      display: grid;
      grid-auto-flow: column;
      grid-auto-columns: max-content;
      grid-auto-rows: get-vw(42px);

      border-radius: get-vw(22px);
      border: get-vw(2px) solid #282B2F;

      button{
        width: 100%;
        height: get-vw(42px);
        padding: 0 get-vw(15px);
        margin-top: get-vw(-2px);
        border-radius: get-vw(22px);
        background-color: transparent;
      }

      button.active-order{
        background-color: #282B2F;
        color: white;
      }

      button:first-child{ margin-left: get-vw(-2px); }
      button:last-child{ margin-left: get-vw(2px); }
    }
  }

  &_change-layout{
    display: flex;
    align-items: center;

    & > div{
      width: max-content;
      height: get-vw(42px);
      margin-left: get-vw(15px);

      display: grid;
      grid-auto-flow: column;
      grid-auto-columns: max-content;
      grid-auto-rows: get-vw(42px);

      border-radius: get-vw(22px);
      border: get-vw(2px) solid #282B2F;

      button,
      label{
        width: 100%;
        height: get-vw(42px);
        padding: 0 get-vw(15px);
        margin-top: get-vw(-2px);
        border-radius: get-vw(22px);
        background-color: transparent;
      }

      label{
        position: relative;
        display: flex;
        align-items: center;

        span{
          margin-right: get-vw(10px);
        }

        input{
          width: get-vw(36px + 8px);
          appearance: none;
          height: get-vw(20px);
          cursor: pointer;

          &::-moz-range-thumb{
            appearance: none;
            width: get-vw(16px);
            height: get-vw(16px);
            background-color: #282B2F;
            border-radius: 50%;
          }

          &::-webkit-slider-thumb{
            appearance: none;
            width: get-vw(16px);
            height: get-vw(16px);
            background-color: #282B2F;
            border-radius: 50%;
          }
        }

        .slider-marks{
          width: get-vw(36px);
          height: get-vw(2px);
          right: get-vw(17px  + 4px);

          position: absolute;
          display: flex;
          justify-content: space-between;
          align-items: center;
          pointer-events: none;

          background-color: #282B2F;

          i{
            width: get-vw(6px);
            height: get-vw(6px);
            border-radius: 50%;
            background-color: #282B2F;
          }
        }
      }

      button.active-layout{
        background-color: #282B2F;
        color: white;
      }

      label.active-layout{
        background-color: #282B2F;
        color: white;

        input{
          &::-moz-range-thumb{
            background-color: white;
          }
          &::-webkit-slider-thumb{
            background-color: white;
          }
        }

        .slider-marks{
          background-color: white;

          i{
            background-color: white;
          }
        }
      }

      & > :first-child{ margin-left: get-vw(-2px); }
      & > :last-child{ margin-left: get-vw(2px); }
    }
  }

  span,
  button,
  label,
  input{
    @include proxima-18;
  }
}

.catalog-container_filters{
  grid-area: filters;
}

#catalog-items-container{
  position: relative;
}

#catalog-items-container.grid-0{
  display: grid;
  grid-auto-flow: row;
  grid-auto-rows: get-vw(760px);
  grid-template-columns: repeat(3, get-vw(600px));
  grid-gap: get-vw(30px);

  &.active-filters{
    grid-template-columns: repeat(2, get-vw(600px));
  }
}

#catalog-items-container.grid-1{
  display: grid;
  grid-auto-flow: row;
  grid-auto-rows: get-vw(560.5px);
  grid-template-columns: repeat(4, get-vw(442.5px));
  grid-gap: get-vw(30px);

  &.active-filters{
    grid-template-columns: repeat(3, get-vw(442.5px));
  }
}

#catalog-items-container.grid-2{
  display: grid;
  grid-auto-flow: row;
  grid-auto-rows: get-vw(442px);
  grid-template-columns: repeat(5, get-vw(348px));
  grid-gap: get-vw(30px);

  &.active-filters{
    grid-template-columns: repeat(4, get-vw(348px));
  }
}

#catalog-items-container.list{
  display: grid;
  grid-auto-flow: row;
  grid-auto-rows: get-vw(562px);
  grid-gap: get-vw(30px);
}
